import axios from 'axios'


export const AUTORIZATION__LOGIN = 'AUTORIZATION__LOGIN'
export const LOGOUT__LOGIN = 'LOGOUT__LOGIN'




export const onCurrentAccount = () => async dispatch => {

  const loginLS = localStorage.getItem('login');
  const tokenLS = localStorage.getItem('token');

  if (loginLS && tokenLS) {
    dispatch({
      type: AUTORIZATION__LOGIN,
      login: loginLS,
      token: tokenLS
    })
  }
}



export const onAuthentication = ({ login, password }, callback) => async dispatch => {
  try {
    const token = await axios.post('/api/authentication', {
      login,
      password
    }).catch(error => { throw error })

    localStorage.setItem('login', login)
    localStorage.setItem('token', token.data)

    dispatch({
      type: AUTORIZATION__LOGIN,
      login,
      token: token.data
    })


    callback(true)
  } catch (error) {
    callback(false)
  }
}




export const onLogoutLogin = () => dispatch => {
  dispatch({
    type: LOGOUT__LOGIN
  })

  localStorage.removeItem('login');
  localStorage.removeItem('token');
}




