import { push } from 'react-router-redux'
import axios from 'axios'

export const GET_USERS__LOADING = 'GET_USERS__LOADING'
export const GET_USERS__SUCCESS = 'GET_USERS__SUCCESS'
export const GET_USERS__ERROR = 'GET_USERS__ERROR'
export const ADD_USERS__LOADING = 'ADD_USERS__LOADING'
export const ADD_USERS__SUCCESS = 'ADD_USERS__SUCCESS'
export const ADD_USERS__ERROR = 'ADD_USERS__ERROR'
export const ADD_USERS__LOGIN_NOTFREE = 'ADD_USERS__LOGIN_NOTFREE'
export const ADD_USERS__LOGIN_FREE = 'ADD_USERS__LOGIN_FREE'
export const EDIT_USER = 'EDIT_USER'
export const SAVE_USER__LOADING = 'SAVE_USER__LOADING'
export const SAVE_USER__SUCCESS = 'SAVE_USER__SUCCESS'
export const SAVE_USER__ERROR = 'SAVE_USER__ERROR'
export const DELETE_USER__LOADING = 'DELETE_USER__LOADING'
export const DELETE_USER__SUCCESS = 'DELETE_USER__SUCCESS'
export const DELETE_USER__ERROR = 'DELETE_USER__ERROR'





export const onChangePageUsers = (pageUsers) => dispatch => {
  dispatch(push('/users/?users=' + pageUsers))
}


// добавляем пользователям свойства, необходимые для фронтенда
function addPropsUsers() {
  this["editing"] = false;  // добавляем, свойство отвечает за редактирование пользователя
  this["loading"] = false;
  this["error"] = false;
}


export const onGetUsers = () => async dispatch => {
  dispatch({ type: GET_USERS__LOADING })

  try {
    const users = await axios.get('/api/users').catch(error => { throw error })

    // добавлем пользователям свойство editing, для отслеживания пользователей 
    // находящихся в процессе редактировани
    for (let i = 0; i < users.data.length; i++) {
      addPropsUsers.call(users.data[i])
    }

    dispatch({
      type: GET_USERS__SUCCESS,
      users: users.data.reverse()
    })
  } catch (error) {
    dispatch({
      type: GET_USERS__ERROR
    })
  }
}


// onAddUsers
export const onAddUsers = (userInput) => async dispatch => {
  dispatch({ type: ADD_USERS__LOADING })

  try {
    const user = await axios.post('/api/users', {
      login: userInput.login,
      privilegien: userInput.privilegien,
      name: userInput.name,
      password: userInput.password
    }).catch(error => { throw error })

    // если на сервере логин занят, то сервер возвращает 'loginNotFree'
    if (String(user.data) === 'loginNotFree') {
      dispatch({
        type: ADD_USERS__LOGIN_NOTFREE
      })
    } else {
      addPropsUsers.call(user.data)
      dispatch({
        type: ADD_USERS__SUCCESS,
        user: user.data
      })
      return true;  // необходимо для того чтобы очищать формы в компонентах
    }

  } catch (error) {
    dispatch({
      type: ADD_USERS__ERROR,
      error: true
    })
  }
}

export const onAddUsers_loginFree = () => dispatch => {
  dispatch({ type: ADD_USERS__LOGIN_FREE })
}
//END onAddUsers


// editing User
export const onEditUser = (id) => dispatch => {
  dispatch({
    type: EDIT_USER,
    id
  })
}



export const onSaveUser = (id, user) => async dispatch => {
  dispatch({
    type: SAVE_USER__LOADING,
    id
  })

  try {
    const userBack = await axios.put('/api/users', {
      id,
      name: user.name,
      privilegien: user.privilegien
    }).catch(error => { throw error })

    addPropsUsers.call(userBack.data)
    dispatch({
      type: SAVE_USER__SUCCESS,
      id,
      user: userBack.data
    })
    return true;
  } catch (error) {
    dispatch({
      type: SAVE_USER__ERROR,
      id
    })
    return false;
  }
}
//END editing User


export const onDeleteUser = (id) => async dispatch => {
  dispatch({ type: DELETE_USER__LOADING })

  try {
    const users = await axios.delete('/api/users/' + id).catch(error => { throw error })

    dispatch({
      type: DELETE_USER__SUCCESS,
      id: users.data
    })
    return true
  } catch (error) {
    dispatch({
      type: DELETE_USER__ERROR
    })
  }
  return false
}






