import React, { Component } from 'react'
import './index.css'
//Universal Components
import PopupDataChanges from 'components/all/PopupDataChanges'

class UsersAdd extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loginInputError: false,
      passwordInputError: false,
      isAddUser: false
    }
  }

  onAddUsers = (e) => {
    e.preventDefault();

    let isInputsError = false;

    this.setState({
      loginInputError: false,
      passwordInputError: false,
      isPopupDataChanges_add: false
    });

    if (this.userLogin.value.trim() === '') {
      this.setState({ loginInputError: true });
      isInputsError = true;
    }
    if (this.userPassword.value.trim() === '') {
      this.setState({ passwordInputError: true });
      isInputsError = true;
    }

    if (!isInputsError) {
      const user = {
        login: this.userLogin.value,
        privilegien: this.userPrivilegien.value,
        name: this.userName.value,
        password: this.userPassword.value
      };

      this.props.onAddUsers(user).then((success) => {
        if (success) {
          this.clearForm()
          this.handleClose__PopupDataChanges()
          this.setState({ isPopupDataChanges_add: true })
        }
      })
    }
  }

  onClear = (e) => {
    e.preventDefault();

    this.clearForm();
  }

  clearForm = () => {
    this.userLogin.value = '';
    this.userPrivilegien.value = '';
    this.userName.value = '';
    this.userPassword.value = '';

    this.setState({
      loginInputError: false,
      passwordInputError: false
    })

    this.props.onAddUsers_loginFree();
  }


  onAddUsers_loginFree = () => {
    this.props.onAddUsers_loginFree();
  }


  handleClose__PopupDataChanges = () => {
    this.setState({ isPopupDataChanges_add: false })
  }

  render() {
    return (
      <div>
        <form onSubmit={this.onAddUsers} className="formAddUser">
          <div className="formAddUser__title">Добавить пользователя:</div>

          <div>
            Логин:
            <input
              autoFocus={true}
              onChange={this.onAddUsers_loginFree}
              ref={input => this.userLogin = input} />
            {this.state.loginInputError && <label>Заполните поле</label>}
          </div>

          <div>
            Привилегии:
            <input ref={input => this.userPrivilegien = input} />
          </div>

          <div>
            ФИО:
            <input ref={input => this.userName = input} />
          </div>

          <div>
            Пароль:
            <input ref={input => this.userPassword = input} type="password" />
            {this.state.passwordInputError && <label>Заполните поле</label>}
          </div>

          <button type="submit" className="formAddUser__button" >Добавить пользователя</button>
          <button onClick={this.onClear} className="formAddUser__button" >Очитска</button>
        </form>

        {this.props.addUser.get('loading') ? <div>идет загрузка</div> : null}
        {this.props.addUser.get('error') ? <div>произошла ошибка</div> : null}
        {this.props.addUser.get('loginNotFree') ? <div>логин уже занят</div> : null}

        <PopupDataChanges
          isActive={this.state.isPopupDataChanges_add}
          typeDataChanges='add'
          handleClosePopup={this.handleClose__PopupDataChanges} />
      </div>
    )
  }
}


export default UsersAdd;



