import React, { Component } from 'react'
import ImmutablePropTypes from 'react-immutable-proptypes'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import './index.css'
import {
  onChangePageUsers,
  onGetUsers,
  onAddUsers,
  onAddUsers_loginFree,
  onEditUser,
  onSaveUser,
  onDeleteUser
} from 'actions/users'
//Components
import UsersAdd from 'components/Users/UsersAdd';
import UsersList from 'components/Users/UsersList';


class Users extends Component {
  componentDidMount() {
    this.props.onGetUsers();
  }

  render() {
    return (
      <div>
        <UsersAdd
          addUser={this.props.addUser}
          onAddUsers_loginFree={this.props.onAddUsers_loginFree}
          onAddUsers={(user) => this.props.onAddUsers(user)}
        />

        <UsersList
          usersList={this.props.usersList}
          usersList_loading={this.props.usersList_loading}
          usersList_error={this.props.usersList_error}
          pageUsers={this.props.pageUsers}
          onChangePageUsers={(pageUsers) => this.props.onChangePageUsers(pageUsers)}
          onEditUser={(id) => this.props.onEditUser(id)}
          onSaveUser={(id, user) => this.props.onSaveUser(id, user)}
          onDeleteUser={(id) => this.props.onDeleteUser(id)}
        />

        {/* <div>
          <button onClick={() => fetchDog(this.props.dispatch)}>Show Dog</button>
          {this.props.loading
            ? <p>Loading...</p>
            : this.props.error
              ? <p>Error, try again</p>
              : <p><img src={this.props.url} /></p>}
        </div> */}

      </div>
    )
  }
}


Users.propTypes = {
  pageNews: PropTypes.number,
  usersList_loading: PropTypes.bool.isRequired,
  usersList_error: PropTypes.bool.isRequired,

  addUser: ImmutablePropTypes.mapContains({
    loading: PropTypes.bool,
    error: PropTypes.bool,
    loginNotFree: PropTypes.bool,
  }).isRequired,

  usersList: ImmutablePropTypes.listOf(
    ImmutablePropTypes.mapContains({
      id: PropTypes.number.isRequired,
      login: PropTypes.string.isRequired,
      name: PropTypes.string.isRequired,
      privilegien: PropTypes.string.isRequired,
      date_create: PropTypes.string,
      date_last_visit: PropTypes.string,
      editing: PropTypes.bool.isRequired,
      loading: PropTypes.bool.isRequired,
      error: PropTypes.bool.isRequired,
    })
  ).isRequired
}


const mapStateToProps = state => ({
  usersList: state.users.get('users'),
  usersList_loading: state.users.get('loading'),
  usersList_error: state.users.get('error'),
  addUser: state.users.get('addUsers'),
  pageUsers: Number(state.routing.locationBeforeTransitions.query.users) || 1,
})

const mapDispatchToProps = ({
  onChangePageUsers,
  onGetUsers,
  onAddUsers,
  onAddUsers_loginFree,
  onEditUser,
  onSaveUser,
  onDeleteUser
})

export default connect(mapStateToProps, mapDispatchToProps)(Users);



