import {
  ADD_NEWS,
  REWRITE_NEWS,
  GET_NEWS,
  CHECKED_DELETE_NEWS,
  DELETE_NEWS,
  EDIT_FORM_NEWS,
  CLEAR_EDIT_NEWS,
  CHANGE_FROM_NEWS,
  SORT_NEWS
} from 'actions/news'
import { List, Map } from 'immutable'


const initialState = Map({
  formNews: Map({
    id: null,
    author: '',
    title: '',
    text: '',
    checked: false,
  }),
  news: List()
});


export default function news(state = initialState, action) {
  switch (action.type) {
    case CHANGE_FROM_NEWS:
      const formNews = state.get('formNews').set(action.fieldName, action.value);
      return state.set('formNews', formNews);

    case GET_NEWS:
      return state.set('news', action.news);

    case ADD_NEWS:
      const newsAdd = state.get('news').unshift(Map(action.news));
      return state.set('news', newsAdd);

    case REWRITE_NEWS:
      const newsRewrite = state.get('news').map(news => {
        if (Number(news.get('id')) === Number(action.id)) {
          return Map(action.news)
        } else {
          return news
        }
      });
      return state.set('news', newsRewrite);

    case CHECKED_DELETE_NEWS:
      const newsChecked = state.get('news').map(news => {
        if (Number(news.get('id')) === Number(action.id)) {
          return news.update('checked', checked => !checked)
        } else {
          return news
        }
      });
      return state.set('news', newsChecked);

    case DELETE_NEWS:
      const newsDelete = state.get('news').filter(news => Boolean(news.get('checked')) === false);
      return state.set('news', newsDelete);

    case SORT_NEWS:
      const sortNews = state.get('news').sort(function (a, b) {
        if (a.get(action.value) > b.get(action.value)) {
          return 1;
        }
        if (a.get(action.value) < b.get(action.value)) {
          return -1;
        }
        return 0;
      });
      return state.set('news', sortNews);


    case EDIT_FORM_NEWS:
      return state.set('formNews', Map(action.formNews));

    case CLEAR_EDIT_NEWS:
      return state.set('formNews', Map(action.formNews));

    default:
      return state;
  }
}




