import {
  GET_PAGES__SUCCESS,
  ADD_PAGES__LOADING,
  ADD_PAGES__SUCCESS,
  ADD_PAGES__ERROR,
  ADD_PAGES__ALIAS_NOTFREE,
  ADD_PAGES__ALIAS_FREE,
  DELETE_PAGE__SUCCESS,
  SAVE_PAGE__SUCCESS
} from 'actions/pages'


const initialState = [];


export default function pages(state = initialState, action) {
  switch (action.type) {
    case GET_PAGES__SUCCESS:
      return [...action.pages];

    case ADD_PAGES__LOADING:
      return state;

    case ADD_PAGES__SUCCESS:
      return [action.page, ...state];

    case ADD_PAGES__ERROR:
      return state;


    case ADD_PAGES__ALIAS_NOTFREE:
      return state;

    case ADD_PAGES__ALIAS_FREE:
      return state;


    case SAVE_PAGE__SUCCESS:
      const savePage = state.map(item => {
        if (Number(item.id) === Number(action.id)) {
          return action.page
        } else {
          return item
        }
      })
      return savePage;


    case DELETE_PAGE__SUCCESS:
      const arrPagesDelete = state.filter(item => Number(item.id) !== Number(action.id));
      return [...arrPagesDelete];


    default:
      return state;
  }
}



