import React, { Component } from 'react'

import './index.css'

class PagesItemEdit extends Component {
  constructor(props) {
    super(props)

    this.state = {
      loading: false,
      error: false,
      nameInputError: false,
      textInputError: false
    }
  }


  onSaveEditPage = (e) => {
    e.preventDefault();

    this.setState({
      loading: true,
      error: false
    })

    const validationSuccess = this.validationForm();

    if (validationSuccess) {
      const id = this.props.pagesItem.id;
      const page = {
        name: this.pageName.value,
        text: this.pageText.value
      }

      this.props.onSaveEditPage(id, page).then((success) => {
        if (success) {
          this.setState({ loading: false })
          this.props.handleCancel(); // закрываем форму редактирования
          this.props.handleOpen__PopupDataChanges__edit(); // открываем попап сигнализирующий об успешном изменении
        } else {
          this.setState({
            loading: false,
            error: true
          })
        }
      })
    } else {
      this.setState({ loading: false })
    }
  }

  validationForm = () => {
    let isValidationSuccess = true;

    this.setState({
      nameInputError: false,
      textInputError: false
    })

    if (this.pageName.value.trim() === '') {
      this.setState({ nameInputError: true });
      isValidationSuccess = false;
    }
    if (this.pageText.value.trim() === '') {
      this.setState({ textInputError: true });
      isValidationSuccess = false;
    }

    return isValidationSuccess ? true : false
  }


  render() {
    return (
      <form onSubmit={this.onSaveEditPage} >
        <div>
          <label>Наименование:</label>
          <input
            autoFocus={true}
            type="text"
            defaultValue={this.props.pagesItem.name}
            ref={input => this.pageName = input} />
          {this.state.nameInputError && <label>Заполните поле</label>}
        </div>

        <div>
          <label>Псевдоним:</label>
          <div>{this.props.pagesItem.alias}</div>
        </div>

        <div>
          <label>Текст:</label><br />
          <textarea
            type="text"
            defaultValue={this.props.pagesItem.text}
            ref={input => this.pageText = input} />
          <br />
          {this.state.textInputError && <label>Заполните поле</label>}
        </div>

        {this.state.loading ?
          <div>
            <button disabled>Сохранить</button>
            <button disabled>Отмена</button>
          </div>
          :
          <div>
            <button type="submit">Сохранить</button>
            <button onClick={this.props.handleCancel}>Отмена</button>
          </div>
        }

        <br />
        {this.state.error && <div>Произошла ошибка, попробуйте позже!</div>}
      </form>
    )
  }
}

export default PagesItemEdit


