import { ADD_LIKE, ADD_DISLIKE } from 'actions/likes';
import { Map } from 'immutable';


const initialState = Map({
  views: 0,
  likes: 0,
  dislikes: 0,
});


export default function likes(state = initialState, action) {
  switch (action.type) {
    case ADD_LIKE:
      return state.update('likes', likes => likes + 1);

    case ADD_DISLIKE:
      return state.update('dislikes', dislikes => dislikes + 1);

    default:
      return state;
  }
}



