import React, { Component } from 'react'

import './index.css';


class HomeContainer extends Component {
  render() {
    return (
      <div>
        <div>
          <h2>В проекте имеется 3 формы добавления и отображения:</h2>
          <ul>
            <li>новости</li>
            <li>пользователи</li>
            <li>страницы</li>
          </ul>
          <p>
            При разработке каждой форм был применен уникальный подход для каждой из них.
          </p>
        </div>

        <div style={{ height: '2px', backgroundColor: 'white', white: 'auto', margin: '40px 0' }}></div>

        <div>
          <h4>Новости:</h4>
          <ul>
            <li>сохраняет состояние формы добавления независимо от перемещения по приложению.</li>
            <li>приминен immutable.js</li>
          </ul>
        </div>

        <div>
          <h4>Пользователи:</h4>
          <ul>
            <li>приминен immutable.js</li>
          </ul>
        </div>

        <div>
          <h4>Страницы:</h4>
          <ul>
            <li>добавление "страницы" происходит на отдельной странице</li>
            <li>изменение "страницы" происходит на отдельной странице</li>
            <li>загрузка и ошибки при загрузке не хронятся в сторе, а обрабатываются в стейте</li>
          </ul>
        </div>

      </div>
    )
  }
}


export default HomeContainer;



