import React, { Component } from 'react'
import ImmutablePropTypes from 'react-immutable-proptypes'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import './index.css'
import {
  onGetNews,
  onChangePageNews,
  onAddNews,
  onEditFormNews,
  onRewriteNews,
  onClearEditNews,
  onDeleteNews,
  onCheckedDeleteNews,
  onChangeFormNews,
  onSortNews
} from 'actions/news'
//Universal Components
import PopupDataChanges from 'components/all/PopupDataChanges'
//Components
import NewsAdd from 'components/News/NewsAdd'
import NewsList from 'components/News/NewsList'

class NewsContainer extends Component {
  constructor(props) {
    super(props)

    this.state = {
      isPopupDataChanges_delete: false
    }
  }

  componentDidMount() {
    this.props.onGetNews();
  }

  onDeleteNews = (e) => {
    e.preventDefault();

    if (window.confirm('Удалить данные, вы уверены?')) {
      this.props.onDeleteNews();
      setTimeout(() => {
        this.handleClose__PopupDataChanges()
        this.setState({ isPopupDataChanges_delete: true })
      })
    }
  }

  onSortNews = (e) => {
    e.preventDefault();

    const value = e.target.value;
    this.props.onSortNews(value);
  }

  handleClose__PopupDataChanges = () => {
    this.setState({
      isPopupDataChanges_delete: false
    })
  }

  render() {
    return (
      <div>
        <NewsAdd // сохранение состояния даже при изменениее маршрутов
          formNews={this.props.formNews}
          onChangeFormNews={(fieldName, value) => this.props.onChangeFormNews(fieldName, value)}
          onClearEditNews={this.props.onClearEditNews}
          onRewriteNews={this.props.onRewriteNews}
          onAddNews={this.props.onAddNews} />

        <div className="news__sort">
          Сорторивать по:
          <select className="news__sort_select" onChange={this.onSortNews}>
            <option disabled>Выберите тип сортировки:</option>
            <option value='id'>Дате добавления</option>
            <option value='author'>Автору</option>
            <option value='title'>Названию статьи</option>
          </select>
        </div>

        <NewsList
          pageNews={this.props.pageNews}
          news={this.props.news}
          onCheckedDeleteNews={(id) => this.props.onCheckedDeleteNews(id)}
          onChangePageNews={(pageNews) => this.props.onChangePageNews(pageNews)}
          onEditFormNews={(id) => this.props.onEditFormNews(id)} />

        <button onClick={this.onDeleteNews}>Удалить выбранные новости</button>

        <div className="news__count">
          Всего новостей: {this.props.news.count()}
        </div>

        <PopupDataChanges
          isActive={this.state.isPopupDataChanges_delete}
          typeDataChanges='delete'
          textPopup="Данные удаленны!"
          handleClosePopup={this.handleClose__PopupDataChanges} />
      </div>
    )
  }
}

NewsContainer.propTypes = {
  pageNews: PropTypes.number,
  formNews: ImmutablePropTypes.mapContains({
    id: PropTypes.number,
    author: PropTypes.string,
    title: PropTypes.string,
    text: PropTypes.string,
    checked: PropTypes.bool,
  }),
  news: ImmutablePropTypes.listOf(
    ImmutablePropTypes.mapContains({
      id: PropTypes.number.isRequired,
      author: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      text: PropTypes.string.isRequired,
      checked: PropTypes.bool.isRequired,
    })
  ).isRequired
}

const mapStateToProps = state => ({
  formNews: state.news.get('formNews'),
  news: state.news.get('news'),
  pageNews: Number(state.routing.locationBeforeTransitions.query.news) || 1
})

const mapDispatchToProps = ({
  onGetNews,
  onAddNews,
  onCheckedDeleteNews,
  onDeleteNews,
  onChangePageNews,
  onEditFormNews,
  onRewriteNews,
  onClearEditNews,
  onChangeFormNews,
  onSortNews
})


export default connect(mapStateToProps, mapDispatchToProps)(NewsContainer);



