import axios from 'axios'
import { push } from 'react-router-redux'

export const GET_PAGES__SUCCESS = 'GET_PAGES__SUCCESS'
export const ADD_PAGES__LOADING = 'ADD_PAGES__LOADING'
export const ADD_PAGES__SUCCESS = 'ADD_PAGES__SUCCESS'
export const ADD_PAGES__ERROR = 'ADD_PAGES__ERROR'
export const ADD_PAGES__ALIAS_NOTFREE = 'ADD_PAGES__ALIAS_NOTFREE'
export const ADD_PAGES__ALIAS_FREE = 'ADD_PAGES__ALIAS_FREE'
export const DELETE_PAGE__SUCCESS = 'DELETE_PAGE__SUCCESS'
export const SAVE_PAGE__SUCCESS = 'SAVE_PAGE__SUCCESS'



// pagination
export const onChangePaginationPages = (paginationPage) => dispatch => {
  dispatch(push('/pages/?pages=' + paginationPage))
}
//END pagination


export const onGetPages = () => async dispatch => {
  try {
    const pages = await axios.get('/api/pages').catch(error => { throw error })

    dispatch({
      type: GET_PAGES__SUCCESS,
      pages: pages.data.reverse()
    })
    return true
  } catch (error) {
    return false
  }
}


// onAddPages
export const onAddPages = (pageInput) => async dispatch => {
  dispatch({ type: ADD_PAGES__LOADING })

  try {
    const page = await axios.post('/api/pages', {
      name: pageInput.name,
      alias: pageInput.alias,
      text: pageInput.text
    }).catch(error => { throw error })

    // если на сервере alias страницы занят, то сервер возвращает 'aliasNotFree'
    if (String(page.data) === 'aliasNotFree') {
      dispatch({
        type: ADD_PAGES__ALIAS_NOTFREE
      })
      return 'aliasNotFree';
    } else {
      // addPropsPages.call(page.data)
      dispatch({
        type: ADD_PAGES__SUCCESS,
        page: page.data
      })
      return true;  // необходимо для того чтобы очищать формы в компонентах
    }

  } catch (error) {
    dispatch({
      type: ADD_PAGES__ERROR,
      error: true
    })
    return false; // сигнализирует форме об ошибке
  }
}

export const onAddPages_aliasFree = () => dispatch => {
  dispatch({ type: ADD_PAGES__ALIAS_FREE })
}
//END onAddPages




// editing Page
export const onSaveEditPage = (id, page) => async dispatch => {
  try {
    const pageBack = await axios.put('/api/pages', {
      id,
      name: page.name,
      text: page.text
    }).catch(error => { throw error })

    dispatch({
      type: SAVE_PAGE__SUCCESS,
      id,
      page: pageBack.data
    })
    return true
  } catch (error) {
    return false
  }
}
//END editing Page


export const onDeletePage = (id) => async dispatch => {
  try {
    const pages = await axios.delete('/api/pages/' + id).catch(error => { throw error })
    dispatch({
      type: DELETE_PAGE__SUCCESS,
      id: pages.data
    })
    return true
  } catch (error) {
    return false
  }
}



