import React, { Component } from 'react'
import { Link } from 'react-router'
import Pagination from "react-js-pagination"

import './index.css'


class PagesList extends Component {

  render() {
    // pagination
    const per_page = 5;
    const itemsCount = this.props.pagesList.length;
    const current_page = this.props.paginationPages;
    const start_offset = (current_page - 1) * per_page;
    let start_count = 0;
    // END pagination

    return (
      <div>
        <ul>
          {
            this.props.pagesList.map((item, num) => {
              if (num >= start_offset && start_count < per_page) {
                start_count++;

                return (
                  <li key={item.id}>
                    <Link to={'/pages/' + item.id}>{item.name}</Link>
                    <br />
                    Псевдоним: <span>{item.alias}</span>
                    <br />
                    <br />
                  </li>
                )
              } else {
                return undefined
              }
            })
          }
        </ul>

        <Pagination
          activePage={current_page}
          itemsCountPerPage={per_page}
          totalItemsCount={itemsCount}
          pageRangeDisplayed={3}
          onChange={this.props.onChangePaginationPages} />
      </div>
    )
  }
}

export default PagesList



