import { List, Map } from 'immutable'
import { push } from 'react-router-redux'

import apiNewsData from 'api/newsData'


export const GET_NEWS = 'GET_NEWS'
export const ADD_NEWS = 'ADD_NEWS'
export const REWRITE_NEWS = 'REWRITE_NEWS'
export const EDIT_FORM_NEWS = 'EDIT_FORM_NEWS'
export const CLEAR_EDIT_NEWS = 'CLEAR_EDIT_NEWS'
export const DELETE_NEWS = 'DELETE_NEWS'
export const CHECKED_DELETE_NEWS = 'CHECKED_DELETE_NEWS'
export const CHANGE_FROM_NEWS = 'CHANGE_FROM_NEWS'
export const SORT_NEWS = 'SORT_NEWS'


// pagination
export const onChangePageNews = (pageNews) => dispatch => {
  dispatch(push('/news/?news=' + pageNews))
}
//END pagination

export const onGetNews = () => dispatch => {

  const newsData = List(apiNewsData.reverse().map((item) => {
    return (
      Map({
        id: Number(item.id),
        author: item.author,
        title: item.title,
        text: item.text,
        checked: false
      })
    )
  }))

  dispatch({
    type: GET_NEWS,
    news: newsData
  })
}


// clearEditNews 
const clearEditNews = () => {
  const formNews = {
    id: null,
    author: '',
    title: '',
    text: '',
  }
  return ({
    type: CLEAR_EDIT_NEWS,
    formNews
  })
}

export const onAddNews = () => (dispatch, getState) => {

  const maxId = Math.max(...getState().news.get('news').map((newsState) => {
    return newsState.get('id')
  }))

  dispatch({
    type: ADD_NEWS,
    news: {
      id: Number(maxId + 1),
      author: getState().news.get('formNews').get('author'),
      title: getState().news.get('formNews').get('title'),
      text: getState().news.get('formNews').get('text'),
      checked: false
    }
  })

  dispatch(clearEditNews())
}


export const onRewriteNews = () => (dispatch, getState) => {
  const id = Number(getState().news.get('formNews').get('id'));
  dispatch({
    type: REWRITE_NEWS,
    id,
    news: {
      id,
      author: getState().news.get('formNews').get('author'),
      title: getState().news.get('formNews').get('title'),
      text: getState().news.get('formNews').get('text'),
      checked: getState().news.get('formNews').get('checked'),
    }
  })

  dispatch(clearEditNews());
}


export const onClearEditNews = () => dispatch => {
  dispatch(clearEditNews());
}
//END clearEditNews 


export const onEditFormNews = (id) => (dispatch, getState) => {
  const news = getState().news.get('news').filter(news => Number(news.get('id')) === Number(id));

  const formNews = {
    id: Number(id),
    author: news.get(0).get('author'),
    title: news.get(0).get('title'),
    text: news.get(0).get('text'),
    checked: news.get(0).get('checked'),
  }

  dispatch({
    type: EDIT_FORM_NEWS,
    formNews
  })
}


export const onDeleteNews = () => dispatch => {
  dispatch({
    type: DELETE_NEWS
  })
}


export const onCheckedDeleteNews = (id) => dispatch => {
  dispatch({
    type: CHECKED_DELETE_NEWS,
    id
  })
}


export const onChangeFormNews = (fieldName, value) => dispatch => {
  dispatch({
    type: CHANGE_FROM_NEWS,
    fieldName,
    value
  })
}


export const onSortNews = (value) => dispatch => {
  dispatch({
    type: SORT_NEWS,
    value
  })
}



