import React, { Component } from 'react'
import { connect } from 'react-redux'


import './index.css';
import {
  onAddPages,
  onAddPages_aliasFree,
} from 'actions/pages'
//Components
import PagesAdd from 'components/PagesAdd'


class PagesAddContainer extends Component {

  handleGoUrlPages = (e) => {
    e.preventDefault();
    this.props.router.push('/pages');
  }

  render() {
    return (
      <div>
        <button onClick={this.handleGoUrlPages}>список страниц</button>
        <br />
        <br />
        <PagesAdd
          onAddPages={(page) => this.props.onAddPages(page)}
          onAddPages_aliasFree={this.props.onAddPages_aliasFree} />
      </div>
    )
  }
}


const mapDispatchToProps = ({
  onAddPages,
  onAddPages_aliasFree
})

export default connect(null, mapDispatchToProps)(PagesAddContainer);




