import React, { Component } from 'react'
import ImmutablePropTypes from 'react-immutable-proptypes'
import PropTypes from 'prop-types'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import Pagination from "react-js-pagination"

import './index.css'

class NewsList extends Component {

  onCheckedNews = (e) => {
    const id = e.target.value;
    this.props.onCheckedDeleteNews(id);
  }

  onEditFormNews = (e) => {
    const id = e.target.value;
    this.props.onEditFormNews(id);
  }

  render() {

    const transitionOptions = {
      transitionName: 'newsAnimation',
      //transitionAppearTimeout: 500, // обрабатывает первую загрузку компонента
      transitionEnterTimeout: 500,
      transitionLeaveTimeout: 500
    }

    // pagination
    const per_page = 5;
    const itemsCount = this.props.news.count();
    const current_page = this.props.pageNews;
    const start_offset = (current_page - 1) * per_page;
    let start_count = 0;
    // END pagination

    const newsSection = this.props.news.map((news, num) => {
      if (num >= start_offset && start_count < per_page) {
        start_count++;

        return (
          <li key={news.get('id')} >
            <input type="checkbox"
              value={news.get('id')}
              checked={news.get('checked')}
              onChange={this.onCheckedNews} />


            <div> {news.get('title')} </div>
            <div> {news.get('text')} </div>
            <div> {news.get('author')} </div>
            <button value={news.get('id')} onClick={this.onEditFormNews}> Редактировать </button>
          </li>
        )
      } else {
        return undefined;
      }
    })

    return (
      <div>
        <ReactCSSTransitionGroup component="ul" {...transitionOptions}>
          {newsSection}
        </ReactCSSTransitionGroup>

        <Pagination activePage={current_page}
          itemsCountPerPage={per_page}
          totalItemsCount={itemsCount}
          pageRangeDisplayed={3}
          onChange={this.props.onChangePageNews}
        />
      </div>
    )
  }
}

NewsList.propTypes = {
  pageNews: PropTypes.number,
  news: ImmutablePropTypes.listOf(
    ImmutablePropTypes.mapContains({
      id: PropTypes.number.isRequired,
      author: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      text: PropTypes.string.isRequired,
      checked: PropTypes.bool.isRequired,
    })
  ).isRequired
}

export default NewsList


