import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router'
import PropTypes from 'prop-types'


import './index.css';
import {
  onGetPages,
  onChangePaginationPages
} from 'actions/pages'
//Components
import PagesList from 'components/Pages/PagesList'


class PagesContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: true,
      errorLoading: false
    }
  }

  componentDidMount() {
    this.props.onGetPages().then((success) => {
      !success && this.setState({ errorLoading: true });
      this.setState({ loading: false });
    })
  }

  render() {
    if (this.state.loading) {
      return (
        <div>
          загрузка страницы
        </div>
      )
    } else if (this.state.errorLoading) {
      return (
        <div>
          произошла ошибка загрузки страницы
        </div>
      )
    } else {
      return (
        <div>
          <div className="blockLinks">
            <Link to="/pages-add" className="linkPagesAdd">Добавить страницу</Link>
          </div>

          <PagesList
            pagesList={this.props.pagesList}
            paginationPages={this.props.paginationPages}
            onChangePaginationPages={(paginationPages) => this.props.onChangePaginationPages(paginationPages)} />

        </div>
      )
    }
  }
}


PagesContainer.propTypes = {
  paginationPages: PropTypes.number,
  pagesList: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
      alias: PropTypes.string,
      text: PropTypes.string
    })
  )
}


const mapStateToProps = (state) => ({
  pagesList: state.pages,
  paginationPages: Number(state.routing.locationBeforeTransitions.query.pages) || 1,
})

const mapDispatchToProps = ({
  onGetPages,
  onChangePaginationPages
})

export default connect(mapStateToProps, mapDispatchToProps)(PagesContainer);


// export default (
