import React, { Component } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'


import './index.css'
import {
  onGetPages,
  onDeletePage,
  onSaveEditPage
} from 'actions/pages'
//Universal Components
import PopupDataChanges from 'components/all/PopupDataChanges'
//Components
import PagesItem from 'components/PagesItem'
import PagesItemEdit from 'components/PagesItemEdit'

class PagesItemContainer extends Component {
  constructor(props) {
    super(props)

    this.state = {
      loading: true,
      errorLoadig: false,
      editPage: false,
      // isPopupDataChanges изменяем в странице "редактирования", а сама форма добавлена на страницу "по умолчанию"
      isPopupDataChanges__edit: false,
      isPopupDataChanges__delete: false
    }
  }


  componentDidMount() {
    if (!this.props.pagesList.length) { // проверяем существование redux_state.pages
      this.props.onGetPages().then((success) => {
        !success && this.setState({ errorLoading: true });
        this.setState({ loading: false });
      })
    } else {
      this.setState({ loading: false });
    }
  }

  handleGoUrlPages = (e) => {
    e.preventDefault();
    this.props.router.push('/pages');
  }


  handleEditPage = (e) => {
    e.preventDefault();
    this.setState({ editPage: true })
  }

  handleCancel = () => {
    this.setState({ editPage: false })
  }


  //PopupDataChanges
  // приходят ответ из компонента о том что форма закрыта
  handleOpen__PopupDataChanges__edit = () => {
    this.handleClose__PopupDataChanges()
    this.setState({ isPopupDataChanges__edit: true })
  }

  handleOpen__PopupDataChanges__delete = () => {
    this.handleClose__PopupDataChanges()
    this.setState({ isPopupDataChanges__delete: true })
  }

  handleClose__PopupDataChanges = () => {
    this.setState({
      isPopupDataChanges__edit: false,
      isPopupDataChanges__delete: false
    })
  }
  //END PopupDataChanges




  render() {
    const templateUrlBack = (
      <div>
        <button onClick={this.handleGoUrlPages}>список страниц</button>
        <br />
        <br />
      </div>
    )

    if (this.state.loading) {
      // загрузка страницы
      return (
        <div>
          загрузка страницы
        </div>
      )
    } else if (this.state.errorLoading) {
      // ошибка загрузки страницы
      return (
        <div>
          произошла ошибка загрузки страницы
        </div>
      )
    } else if (this.state.editPage) {
      // страница в режиме редактирования
      return (
        <div>
          {templateUrlBack}

          <PagesItemEdit
            pagesItem={this.props.pagesItem}
            handleOpen__PopupDataChanges__edit={this.handleOpen__PopupDataChanges__edit}
            handleCancel={this.handleCancel}
            onSaveEditPage={this.props.onSaveEditPage} />
        </div>
      )
    } else {
      // страница по умолчанию
      return (
        <div>
          {templateUrlBack}

          <PagesItem
            pagesItem={this.props.pagesItem}
            handleEditPage={this.handleEditPage}
            handleOpen__PopupDataChanges__delete={this.handleOpen__PopupDataChanges__delete}
            onDeletePage={this.props.onDeletePage} />

          <PopupDataChanges
            isActive={this.state.isPopupDataChanges__edit}
            typeDataChanges='edit'
            // color="red"
            // backgroundColor="blue"
            // textPopup="Данные измен."
            // closingTime='300'
            handleClosePopup={this.handleClose__PopupDataChanges} />


          <PopupDataChanges
            isActive={this.state.isPopupDataChanges__delete}
            typeDataChanges='delete'
            handleClosePopup={this.handleClose__PopupDataChanges} />
        </div>
      )
    }
  }
}


PagesItemContainer.propTypes = {
  pagesItem: PropTypes.oneOfType([
    () => { return null },
    PropTypes.number
  ]),
  pagesList: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      name: PropTypes.string,
      alias: PropTypes.string,
      text: PropTypes.string
    })
  )
}


const mapStateToProps = (state, ownProps) => {
  let pagesItem = null;
  for (const page of state.pages) {
    if (Number(page.id) === Number(ownProps.params.id)) {
      pagesItem = page;
      break;
    }
  }

  return {
    pagesList: state.pages,
    pagesItem
  }
}

const mapDispatchToProps = ({
  onGetPages,
  onDeletePage,
  onSaveEditPage
})

export default connect(mapStateToProps, mapDispatchToProps)(PagesItemContainer)




