import React, { Component } from 'react'

import './index.css'

class PagesItem extends Component {
  constructor(props) {
    super(props)

    this.state = {
      loading: false,
      error: false
    }
  }

  onDeletePage = (e) => {
    e.preventDefault();
    this.setState({
      loading: true,
      error: false
    })



    if (window.confirm("Подтвердите удаление?")) {
      const id = this.props.pagesItem.id;
      this.props.onDeletePage(id).then((success) => {
        if (success) {
          this.setState({ loading: false })
          this.props.handleOpen__PopupDataChanges__delete(); // открываем попап сигнализирующий об успешном удалении
        } else {
          this.setState({
            loading: false,
            error: true
          })
        }
      })
    } else {
      this.setState({ loading: false })
    }
  }

  render() {
    if (this.props.pagesItem) {
      return (
        <div>
          <label>Наименование:</label>
          <div>{this.props.pagesItem.name}</div>
          <br />
          <label>Псевдоним:</label>
          <div>{this.props.pagesItem.alias}</div>
          <br />
          <label>Текст:</label>
          <div dangerouslySetInnerHTML={{ __html: this.props.pagesItem.text }} />
          <br />

          {this.state.loading ?
            <div>
              <button disabled>Редактировать</button>
              <button disabled>Удалить</button>
            </div>
            :
            <div>
              <button onClick={this.props.handleEditPage}>Редактировать</button>
              <button onClick={this.onDeletePage}>Удалить</button>
            </div>
          }

          <br />
          {this.state.error && <div>Произошла ошибка, попробуйте позже!</div>}
        </div>
      )
    } else {
      return (
        <div>
          Страница не существует!
        </div>
      )
    }
  }
}

export default PagesItem


