import React, { Component } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import './index.css';
import {
  // onAutorizationLogin,
  onAuthentication,
  onLogoutLogin,
  onCurrentAccount
} from 'actions/login'


class LoginContainer extends Component {
  constructor(props) {
    super(props);

    this.props.onCurrentAccount();
  }


  onAuthentication = (e) => {
    e.preventDefault();

    const login = this.userLogin.value;
    const password = this.userPassword.value;

    this.props.onAuthentication({ login, password }, (success) => {
      if (!success) alert('Имя пользователя или пароль не совпадает')
    });
  }


  onLogoutLogin = (e) => {
    e.preventDefault();
    this.props.onLogoutLogin();
  }


  render() {
    if (this.props.login) {
      return (
        <div>
          <button onClick={this.onLogoutLogin}>Выйти</button>
        </div>
      )
    } else {
      return (
        <div>
          <p>
            для входа необходимо ввести
            <br />
            логин: "admin",
            <br />
            пароль любой
          </p>
          <input
            autoFocus={true}
            className="login__inpName"
            placeholder="Login"
            defaultValue="admin"
            ref={input => this.userLogin = input} />

          <input
            className="login__inpPassword"
            type="password"
            placeholder="Password"
            ref={input => this.userPassword = input} />

          {/* <button onClick={this.onAutorizationLogin}>Войти</button> */}
          <button onClick={this.onAuthentication}>Войти</button>
        </div>
      )
    }
  }
}


LoginContainer.propTypes = {
  login: PropTypes.string.isRequired
}


const mapStateToProps = state => ({
  login: state.login.get('login')
})

const mapDispatchToProps = ({
  // onAutorizationLogin,
  onAuthentication,
  onLogoutLogin,
  onCurrentAccount
})

export default connect(mapStateToProps, mapDispatchToProps)(LoginContainer);



