import React, { Component } from 'react'
import Pagination from "react-js-pagination"

//Universal Components
import PopupDataChanges from 'components/all/PopupDataChanges'

class UsersList extends Component {
  constructor(props) {
    super(props)

    this.state = {
      isPopupDataChanges_edit: false,
      isPopupDataChanges_delete: false,
    }
  }

  onEditUser = (e) => {
    e.preventDefault();
    const id = e.target.value;
    this.props.onEditUser(id);
  }

  onCancelUser = (e) => {
    e.preventDefault();
    const id = e.target.value;
    this.props.onEditUser(id);
  }

  onSaveUser = (e) => {
    e.preventDefault();
    const id = e.target.value;
    const user = {
      name: this['userName' + id].value,
      privilegien: this['userPrivilegien' + id].value,
    };
    this.props.onSaveUser(id, user).then((success) => {
      if (success) {
        this.handleClose__PopupDataChanges()
        this.setState({ isPopupDataChanges_edit: true })
      }
    })
  }

  onDeleteUser = (e) => {
    e.preventDefault();

    if (window.confirm("Подтвердите удаление?")) {
      const id = e.target.value;
      this.props.onDeleteUser(id).then((success) => {
        if (success) {
          this.handleClose__PopupDataChanges()
          this.setState({ isPopupDataChanges_delete: true })
        }
      })
    }
  }

  handleClose__PopupDataChanges = () => {
    this.setState({
      isPopupDataChanges_edit: false,
      isPopupDataChanges_delete: false,
    })
  }

  render() {


    // pagination
    const per_page = 8;
    const itemsCount = this.props.usersList.count();
    const current_page = this.props.pageUsers;
    const start_offset = (current_page - 1) * per_page;
    let start_count = 0;
    // END pagination

    // list of users
    const usersTemplate = this.props.usersList.map((item, num) => {
      //pagination - if
      if (num >= start_offset && start_count < per_page) {
        start_count++;

        if (item.get('editing')) {
          return (
            <li key={item.get('id')}>
              <form>
                <div>{item.get('login')}</div>
                <input
                  autoFocus={true}
                  defaultValue={item.get('name')}
                  placeholder="ФИО"
                  ref={input => this['userName' + item.get('id')] = input} />
                <input
                  defaultValue={item.get('privilegien')}
                  placeholder="Привилегии"
                  ref={input => this['userPrivilegien' + item.get('id')] = input} />

                <div>{item.get('dateLastVisit')}</div>

                <button onClick={this.onSaveUser} value={item.get('id')}>Сохранить</button>
                <button onClick={this.onCancelUser} value={item.get('id')}>Отмена</button>
              </form>

              {item.get('loading') && <div>лоадинг</div>}
            </li>
          )
        } else {
          return (
            <li key={item.get('id')}>
              <div> {item.get('login')}</div >
              <div>{item.get('name')}</div>
              <div>{item.get('privilegien')}</div>
              <div>{item.get('dateLastVisit')}</div>

              <button value={item.get('id')} onClick={this.onEditUser}>Редактировать</button>
              <button value={item.get('id')} onClick={this.onDeleteUser}>Удалить</button>
            </li >
          )
        }
        //pagination - else
      } else {
        return undefined;
      }
      //END pagination - if
    })
    //END list of users


    if (this.props.usersList_loading) {
      // show the loading state
      return <div>загрузка...</div>

    } else if (this.props.usersList_error) {
      // show the error state
      return <div>произошла ошибка, попробуйте перезагрузить страницу...</div>

    } else {
      // show the state
      return (
        <div>
          <ul>
            {usersTemplate}
          </ul>

          <Pagination
            activePage={current_page}
            itemsCountPerPage={per_page}
            totalItemsCount={itemsCount}
            pageRangeDisplayed={6}
            onChange={this.props.onChangePageUsers}
          />

          Всего пользователей: {this.props.usersList.count()}

          {/* popup */}
          <PopupDataChanges
            isActive={this.state.isPopupDataChanges_edit}
            typeDataChanges='edit'
            handleClosePopup={this.handleClose__PopupDataChanges} />

          <PopupDataChanges
            isActive={this.state.isPopupDataChanges_delete}
            typeDataChanges='delete'
            handleClosePopup={this.handleClose__PopupDataChanges} />
          {/*END popup */}
        </div>
      )
    }

  }
}


export default UsersList;



