import React from 'react'
import ReactDOM from 'react-dom'
import { createStore, applyMiddleware } from 'redux'
import { composeWithDevTools } from 'redux-devtools-extension'
import { Provider } from 'react-redux'
import { Router, Route, browserHistory } from 'react-router'
import { syncHistoryWithStore, routerMiddleware } from 'react-router-redux'
import { useBasename } from 'history'
import thunk from 'redux-thunk'

import registerServiceWorker from './registerServiceWorker'
import reducer from './node_modules/reducers'
import App from 'containers/App'
import { onCurrentAccount } from './node_modules/actions/login'


import HomeContainer from 'containers/HomeContainer'
import NewsContainer from 'containers/NewsContainer'
import UsersContainer from 'containers/UsersContainer'
import LikesContainer from 'containers/LikesContainer'
import LoginContainer from 'containers/LoginContainer'
import PagesContainer from 'containers/PagesContainer'
import PagesItemContainer from 'containers/PagesItemContainer'
import PagesAddContainer from 'containers/PagesAddContainer'
import NotFoundPage from 'containers/NotFoundPage'


import configChange from 'middlewares/configChange'


const newBrowserHistory = useBasename(() => browserHistory)({
  basename: '/react/admin-redux'
})


// import logger from 'middlewares/logger'
// const middleware = applyMiddleware(thunk, logger, configChange, routerMiddleware(newBrowserHistory));
const middleware = applyMiddleware(thunk, configChange, routerMiddleware(newBrowserHistory));
const store = createStore(reducer, composeWithDevTools(middleware))

const history = syncHistoryWithStore(newBrowserHistory, store)



const authCheck = (nextState, replace) => {

  store.dispatch(onCurrentAccount());

  if (!store.getState().login.get('login')) {
    replace({ pathname: '/login' })
    // replace({ pathname: '/login', query: { return_to: nextState.location.pathname } })
  }
}




ReactDOM.render(
  <Provider store={store}>
    <Router history={history}>
      <Route component={App}>
        <Route path='/' component={HomeContainer} onEnter={authCheck} />
        <Route path='/news' component={NewsContainer} onEnter={authCheck} />
        <Route path='/likes' component={LikesContainer} onEnter={authCheck} />
        <Route path='/users' component={UsersContainer} onEnter={authCheck} />
        <Route path='/pages' component={PagesContainer} onEnter={authCheck} />
        <Route path='/pages(/:id)' component={PagesItemContainer} onEnter={authCheck} />
        <Route path='/pages-add' component={PagesAddContainer} onEnter={authCheck} />
        <Route path='/login' component={LoginContainer} />
        <Route path='*' component={NotFoundPage} onEnter={authCheck} />
      </Route>
    </Router>
  </Provider >,
  document.getElementById('root')
)

// registerServiceWorker();



