import {
  GET_USERS__LOADING,
  GET_USERS__SUCCESS,
  GET_USERS__ERROR,
  ADD_USERS__LOADING,
  ADD_USERS__SUCCESS,
  ADD_USERS__ERROR,
  ADD_USERS__LOGIN_NOTFREE,
  ADD_USERS__LOGIN_FREE,
  EDIT_USER,
  SAVE_USER__LOADING,
  SAVE_USER__SUCCESS,
  SAVE_USER__ERROR,
  DELETE_USER__LOADING,
  DELETE_USER__SUCCESS,
  DELETE_USER__ERROR
} from 'actions/users'
import { fromJS } from 'immutable'
// import { toJS, OrderedMap } from 'immutable'


const initialState = fromJS({
  loading: false,
  error: false,
  users: [],

  addUsers: {
    loading: false,
    error: false,
    loginNotFree: false
  }
});


export default function users(state = initialState, action) {
  switch (action.type) {
    case GET_USERS__LOADING:
      return state.merge(
        fromJS({
          loading: true,
          error: false
        })
      );

    case GET_USERS__SUCCESS:
      return state.merge(
        fromJS({
          loading: false,
          error: false,
          users: action.users
        })
      );

    case GET_USERS__ERROR:
      return state.merge(
        fromJS({
          loading: false,
          error: true
        })
      );


    case ADD_USERS__LOADING:
      return state.merge(
        fromJS({
          addUsers: {
            loading: false,
            error: false,
            loginNotFree: false
          }
        })
      );

    case ADD_USERS__SUCCESS:
      return state.merge(
        fromJS({
          users: [
            action.user,
            ...state.get('users')
          ],
          addUsers: {
            loading: false,
            error: false,
            loginNotFree: false
          }
        })
      );

    case ADD_USERS__ERROR:
      return state.merge(
        fromJS({
          addUsers: {
            error: false,
          }
        })
      );


    case ADD_USERS__LOGIN_NOTFREE:
      return state.merge(
        fromJS({
          addUsers: {
            loading: false,
            error: false,
            loginNotFree: true
          }
        })
      );

    case ADD_USERS__LOGIN_FREE:
      return state.merge(
        fromJS({
          addUsers: {
            loginNotFree: false
          }
        })
      );

    case EDIT_USER:
      const editUsers = state.get('users').map(item => {
        if (Number(item.get('id')) === Number(action.id)) {
          return item.update('editing', editing => !editing)
        } else {
          return item
        }
      });
      return state.set('users', editUsers);


    case SAVE_USER__LOADING:
      const saveUserLoading = state.get('users').map(item => {
        if (Number(item.get('id')) === Number(action.id)) {
          let newItem = item.set('error', false);
          return newItem.set('loading', true)
        } else {
          return item
        }
      });
      return state.set('users', saveUserLoading);

    case SAVE_USER__SUCCESS:
      const saveUser = state.get('users').map(item => {
        if (Number(item.get('id')) === Number(action.id)) {
          return fromJS(action.user)
        } else {
          return item
        }
      });
      return state.set('users', saveUser);

    case SAVE_USER__ERROR:
      const saveUserError = state.get('users').map(item => {
        if (Number(item.get('id')) === Number(action.id)) {
          return item.set('error', true)
        } else {
          return item
        }
      });
      return state.set('users', saveUserError);


    case DELETE_USER__LOADING:
      // не стал реализовывать
      // для реализации необходимо добавить каждому user свойство deleteLoading and deleteError
      return state;

    case DELETE_USER__SUCCESS:
      const userDelete = state.get('users').filter(item => Number(item.get('id')) !== Number(action.id));
      return state.set('users', userDelete);

    case DELETE_USER__ERROR:
      return state;


    default:
      return state;
  }
}






