import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Carousel } from 'react-responsive-carousel'
import "react-responsive-carousel/lib/styles/carousel.min.css"

import { ADD_LIKE, ADD_DISLIKE } from 'actions'
// import './index.css';


class LikesContainer extends Component {
  constructor(props) {
    super(props)

    this.state = {
      carouselData: [
        {
          name: 'Legend 1',
          img: '/img/carousel/1.jpg'
        },
        {
          name: 'Legend 2',
          img: '/img/carousel/2.jpg'
        },
        {
          name: 'Legend 3',
          img: '/img/carousel/3.jpg'
        }
      ]
    }
  }

  onLoadImg = (e) => {
    e.preventDefault();

    this.setState({
      carouselData: [
        ...this.state.carouselData,
        {
          name: 'новый элемент',
          img: '/img/carousel/2.jpg'
          // img: 'this.imgInput.value'
        }
      ]
    })

    this.imgInput.value = '';
  }
  render() {
    return (
      <div>

        {/* <img src="file://C:/1.jpg" alt="" /> */}
        {/* <img src={require("C:/1.jpg")} alt="" /> */}
        {
          /* 
            вроде вебпак не поддерживает пути за пределами дирректории src,
            нужно настроить webPack, либо:
              - подгрузить картинку на сервер
              - создать бекенд для сохранения пути

            P.S.
            после билдинга в таком виде тоже не робит(без изменения в webPack)
          */
        }


        <Carousel>
          {
            this.state.carouselData.map((item, num) => {
              return (
                <div key={num}>
                  <img src={item.img} alt="" />
                  <p className="legend">{item.name}</p>
                </div>
              )
            })
          }
        </Carousel>


        {/* <div>
          <input type="file" name="f" ref={input => this.imgInput = input} />
          <input type="submit" onClick={this.onLoadImg} value="Отправить" />
        </div> */}


        <p>+{this.props.likes} -{this.props.dislikes}</p>
        <div>
          <button onClick={this.props.onLike}>Like</button>
          <button onClick={this.props.onDislike}>Dislike</button>
          {/* <button>Share</button> */}
        </div>
        <br />
        <br />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  likes: state.likes.get('likes'),
  dislikes: state.likes.get('dislikes'),
});

const mapDispatchToProps = dispatch => ({
  onLike: () => dispatch({ type: ADD_LIKE }),
  onDislike: () => dispatch({ type: ADD_DISLIKE }),
});

export default connect(mapStateToProps, mapDispatchToProps)(LikesContainer);



