import React, { Component } from 'react'
import { Field, reduxForm, SubmissionError } from 'redux-form'

import './index.css'
//Components
import PopupDataChanges from 'components/all/PopupDataChanges'

class PagesAdd extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isPopupDataChanges: false
    }
  }

  onSubmit = ({ name = '', alias = '', text = '' }) => {
    let error = {};
    let isError = false;

    if (name.trim() === '') {
      error.name = 'Заполните поле';
      isError = true;
    }

    if (alias.trim() === '') {
      error.alias = 'Заполните поле';
      isError = true;
    }

    if (isError) {
      throw new SubmissionError({ ...error });
    } else {
      const page = { name, alias, text };
      return this.props.onAddPages(page).then((success) => { // return для того чтобы отработал throw
        if (String(success) === 'aliasNotFree') {
          error.alias = 'Измените поле';
          throw new SubmissionError({ ...error, _error: 'Псевдоним страницы не уникален, измените его!' });
        } else if (success) {
          this.props.reset();
          this.setState({ isPopupDataChanges: true });
        } else {
          throw new SubmissionError({ _error: 'Произошла ошибка, страница не добавлена!' });
          // _error - в form можно использовать: this.props.error (общая ошибка)
        }
      })
    }
  }

  renderField = ({ autoFocus, type, label, input, meta: { touched, error } }) => (
    <div>
      <label>{label}</label>
      <input {...input} autoFocus={autoFocus} placeholder={label} type={type} />
      {touched && error && <span>{error}</span>}
    </div>
  )

  handleClose__PopupDataChanges = () => {
    this.setState({ isPopupDataChanges: false })
  }

  render() {
    return (
      <div>
        <form onSubmit={this.props.handleSubmit(this.onSubmit)}>
          <Field
            autoFocus={true}
            name="name"
            label="Название"
            component={this.renderField}
            type="text" />
          <Field
            name="alias"
            label="Псевдоним"
            component={this.renderField}
            type="text" />
          <div>
            <div>Текст</div>
            <Field name="text" component="textarea" type="text" />
          </div>
          <div>
            <button type="submit">Добавить страницу</button>
            <button type="button" onClick={this.props.reset}>Очистка</button>
          </div>
          {this.props.error && <div>{this.props.error}</div>}
        </form>

        <PopupDataChanges
          isActive={this.state.isPopupDataChanges}
          typeDataChanges='add'
          handleClosePopup={this.handleClose__PopupDataChanges} />
      </div>
    )
  }
}


export default reduxForm({ form: 'pages' })(PagesAdd);



