import {
  AUTORIZATION__LOGIN,
  LOGOUT__LOGIN
} from 'actions/login'
import { Map } from 'immutable'


const initialState = Map({
  login: '',
  token: ''
});


export default function login(state = initialState, action) {
  switch (action.type) {
    case AUTORIZATION__LOGIN:
      return state.merge(
        Map({
          login: action.login,
          token: action.token
        })
      );

    case LOGOUT__LOGIN:
      return state.merge(
        Map({
          login: '',
          token: ''
        })
      );

    default:
      return state;
  }
}




