import React, { Component } from 'react'
import ImmutablePropTypes from 'react-immutable-proptypes'
import PropTypes from 'prop-types'

import './index.css'
//Universal Components
import PopupDataChanges from 'components/all/PopupDataChanges'

class NewsAdd extends Component {
  constructor(props) {
    super(props);

    this.state = {
      authorErrorInput: false,
      titleErrorInput: false,
      textErrorInput: false,
      isPopupDataChanges_add: false,
      isPopupDataChanges_edit: false
    }
  }

  onAddNews = (e) => {
    e.preventDefault();

    const successValidation = this.validationForm();

    if (successValidation) {
      this.props.onAddNews();
      this.handleClose__PopupDataChanges();
      this.setState({ isPopupDataChanges_add: true })
    }
  }

  validationForm = () => {
    let successValidation = true;

    this.setState({
      authorErrorInput: false,
      titleErrorInput: false,
      textErrorInput: false
    });

    if (this.props.formNews.get('author') === '') {
      this.setState({ authorErrorInput: true });
      successValidation = false;
    }
    if (this.props.formNews.get('title') === '') {
      this.setState({ titleErrorInput: true });
      successValidation = false;
    }
    if (this.props.formNews.get('text') === '') {
      this.setState({ textErrorInput: true });
      successValidation = false;
    }

    return successValidation ? true : false
  }

  onRewriteNews = (e) => {
    e.preventDefault();

    this.props.onRewriteNews();

    setTimeout(() => {
      this.handleClose__PopupDataChanges()
      this.setState({ isPopupDataChanges_edit: true })
    })
  }

  onClear = (e) => {
    e.preventDefault();

    this.setState({
      authorErrorInput: false,
      titleErrorInput: false,
      textErrorInput: false
    });

    this.props.onClearEditNews();
  }

  onChangeFormNews = (e) => {
    const fieldName = e.target.name;
    const value = e.target.value;
    this.props.onChangeFormNews(fieldName, value)
  }

  handleClose__PopupDataChanges = () => {
    this.setState({
      isPopupDataChanges_add: false,
      isPopupDataChanges_edit: false
    })
  }

  render() {
    return (
      <div>
        <form className="formAddNews">
          <div className="formAddNews__title">Добавить новость:</div>

          <div>
            Автор:
            <input
              autoFocus={true}
              className="formAddNews__inpAutor"
              name="author"
              value={this.props.formNews.get('author')}
              onChange={this.onChangeFormNews} />
            {this.state.authorErrorInput ? <div> <label>Заполните поле</label><br /> </div> : ''}
          </div>

          <div>
            Заголовок:
            <input
              className="formAddNews__inpTitle"
              name="title"
              value={this.props.formNews.get('title')}
              onChange={this.onChangeFormNews} />
            {this.state.titleErrorInput ? <div> <label>Заполните поле</label><br /> </div> : ''}
          </div>

          <div>
            Текст
            <textarea
              className="formAddNews__textNews"
              name="text"
              value={this.props.formNews.get('text')}
              onChange={this.onChangeFormNews} />
            {this.state.textErrorInput ? <div> <label>Заполните поле</label><br /> </div> : ''}
          </div>

          {this.props.formNews.get('id') ?
            <div>
              <button onClick={this.onRewriteNews} className="formAddNews__button">Сохранить новость</button>
              <button onClick={this.onClear}>Отмена</button>
            </div>
            :
            <div>
              <button onClick={this.onAddNews} className="formAddNews__button">Добавить новость</button>
              <button onClick={this.onClear}>Очистка</button>
            </div>
          }
        </form>


        <PopupDataChanges
          isActive={this.state.isPopupDataChanges_add}
          typeDataChanges='add'
          handleClosePopup={this.handleClose__PopupDataChanges} />

        <PopupDataChanges
          isActive={this.state.isPopupDataChanges_edit}
          typeDataChanges='edit'
          handleClosePopup={this.handleClose__PopupDataChanges} />
      </div >
    )
  }
}

NewsAdd.propTypes = {
  formNews: ImmutablePropTypes.mapContains({
    id: PropTypes.number,
    author: PropTypes.string,
    title: PropTypes.string,
    text: PropTypes.string,
    checked: PropTypes.bool,
  })
}

export default NewsAdd;



