import React, { Component } from 'react'
import { Link } from 'react-router'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

import './index.css'

class Navbar extends Component {

  constructor(props) {
    super(props);

    this.state = {
      bigSizeBrowser: true,
      navBarOpen: false
    };
  }

  componentDidMount() {
    window.addEventListener('resize', this.resize);
    this.resize();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resize);
  }

  resize = () => {
    // условия прописаны для однократного рендеринга компонента при изменении размера
    if ((window.innerWidth > 700) && !this.state.bigSizeBrowser) {
      this.setState({ bigSizeBrowser: true });
    } if ((window.innerWidth < 700) && this.state.bigSizeBrowser) {
      this.setState({ bigSizeBrowser: false });
    }
  }

  handleClickNav = (e) => {
    e.preventDefault();
    this.setState({ navBarOpen: !this.state.navBarOpen });
  }

  render() {

    const linkTemplate = (propClassName) => (
      <ul className={propClassName}>
        <li><Link to="/" activeStyle={{ color: 'red' }} className="navLink">Главная</Link></li>
        <li><Link to="/likes" activeStyle={{ color: 'red' }} className="navLink">Лайки</Link></li>
        <li><Link onClick={this.onPageChange} to="/news" activeStyle={{ color: 'red' }} className="navLink">Новости</Link></li>
        <li><Link to="/users" activeStyle={{ color: 'red' }} className="navLink">Пользователи</Link></li>
        <li><Link to="/pages" activeStyle={{ color: 'red' }} className="navLink">Страницы</Link></li>
      </ul>
    );

    const transitionOptions = {
      transitionName: 'navAnimation',
      transitionEnterTimeout: 500,
      transitionLeaveTimeout: 500
    }

    // const transitionOptions2 = {
    //   transitionName: 'navBars',
    //   transitionEnterTimeout: 1000,
    //   transitionLeaveTimeout: 1000
    // }

    if (this.state.bigSizeBrowser) {
      // большей размер экрана
      return (
        <nav className="nav">
          {linkTemplate("nav__ulBigSize")}
        </nav>
      )
    } else if (this.state.navBarOpen) {
      // маленький экран при открытом навбаре
      return (
        <nav className="nav">
          {/* <ReactCSSTransitionGroup {...transitionOptions2}> */}
          <div className="nav__bars">
            <div onClick={this.handleClickNav} className="nav__bars-open">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
          {/* <div className="nav__bars">
              <span onClick={this.handleClickNav} className="nav__bars_close" />
            </div> */}
          {/* </ReactCSSTransitionGroup> */}
          <ReactCSSTransitionGroup {...transitionOptions}>
            {linkTemplate("nav__ulSmallSize")}
          </ReactCSSTransitionGroup>
        </nav>
      )
    } else {
      // маленький экран при закрытом навбаре
      return (
        <nav className="nav">
          {/* <ReactCSSTransitionGroup {...transitionOptions2}> */}
          <div className="nav__bars">
            <div onClick={this.handleClickNav} className="nav__bars-open">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
          {/* </ReactCSSTransitionGroup> */}
          <ReactCSSTransitionGroup {...transitionOptions}>
            {null}
          </ReactCSSTransitionGroup>
        </nav>
      )
    }
  }
}

export default Navbar;




